<template>
  <div class="new">
		<h1>扫一扫零基础学Vue</h1>
		<img src="../../static/images/code.jpg">
  </div>
</template>

<script>
export default {
  name: 'New',
  data () {
    return {
      msg: 'Welcome to Vue.js'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  // 引入 公共样式
  @import '../assets/sass/base.scss';
	.new{
		h1{
			text-align: center;
			line-height:rem(150px);
			color: #333;
		}
		img{
			width: 80%;
			display: block;
			margin: 0 auto;
		}
	}
</style>



