<template>
	<div class="show">
		<div class="content">
			<div class="title">
				<span class="top" v-if='items.top'>置顶</span>
				<span class="top" v-else-if='items.good'>精华</span>
				<span class="other" v-else>{{tabs[items.tab]}}</span>
				<span class="con">{{items.title}}</span>
				<div class="author">
					<span class="dian"></span><span>作者 {{items.author.loginname}}</span>
					<span class="dian"></span><span>{{items.visit_count}}浏览</span>
					<span class="dian"></span><span>来自{{tabs[items.tab]}}</span>
				</div>
			</div>
			<div class="contentt">
				<div class="contentt-con" v-html='items.content'></div>
			</div>
			<div class="replayt">
				<span>{{items.replies.length}}</span>
				<span>回复</span>
			</div>
			<div class="user" v-for='item in items.replies'>
				<div class="replayh">
					<div class="replayl">
						<div class="replayp">
							<img :src="item.author.avatar_url" alt="" />
						</div>
						<div class="replayz">
							<span>{{item.author.loginname}}</span>
							<span class="floor">{{items.replies.indexOf(item)+1
		 					}}楼</span>
						</div>
					</div>
					<div class="replayr">
						<i class="iconfont icon-dianzan"></i>
						<span>{{item.ups.length}}</span>
					</div>
				</div>
				<div class="replay-con" v-html='item.content'>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { getTopicData } from '@/getdata/getTopic.js';
	export default {
		name: 'Show',
		data() {
			return {
				items: [],
				tabs: {
					ask: '问答',
					share: '分享',
					job: '招聘',
					good: '精华'
				}
			}
		},
		mounted: function() {
			var id = this.$route.params.id;
			getTopicData(id).then((response) => {
				console.log(response)
				this.items = response.data.data;
			})
		},
	}
</script>

<style scoped lang="scss">
	// 引入 公共样式
	@import '../../assets/sass/base.scss';
	@import '../../assets/sass/content.scss';
	.show {
		width: rem(730px);
		margin: 0 auto;
		margin-top: rem(90px);
		background: #fff;
		.content {
			padding: rem(15px);
			padding-bottom:rem(105px);
			.title {
				width: 100%;
				border-bottom: rem(1px) solid #aaa;
				span.top {
					display: inline-block;
					padding: rem(5px) rem(10px);
					background: #369219;
					color: #fff;
					line-height: rem(30px);
					text-align: center;
					border-radius: rem(5px);
					margin-top: rem(10px);
				}
				span.other {
					display: inline-block;
					padding: rem(5px) rem(10px);
					background: #e5e5e5;
					color: #999;
					line-height: rem(30px);
					text-align: center;
					border-radius: rem(5px);
					margin-top: rem(10px);
				}
				span.con {
					font-size: rem(35px);
					font-weight: bold;
					margin-left: rem(10px);
				}
				.author {
					line-height: rem(40px);
					display: flex;
					align-items: center;
					margin-bottom: rem(10px);
					span {
						margin: 0 rem(5px);
					}
					
					.dian {
						display: inline-block;
						width: rem(10px);
						height: rem(10px);
						background: #aaa;
						border-radius: 50%;
					}
				}
			}
			.contentt {
				width: 96%;
				margin: rem(30px) auto;
				background: #f7f7f7;
				.contentt-con {
					padding: rem(20px);
					font-size: rem(30px);
					font-family:"宋体";
					line-height: rem(50px);
					color: #222;
					p {
						overflow: hidden;
						background:#fff;
						margin: rem(10px) 0;
						code {
							font-size:rem(30px);
							color: #6e599f;
						}
					}
					img {
						width: 100%;
					}
				}
			}
			.replayt {
				height: rem(60px);
				padding: 0 rem(10px);
				line-height: rem(60px);
				background: #f7f7f7;
				font-size: rem(20px);
			}
			.user {
				padding: rem(10px);
				.replayh {
					height: rem(60px);
					display: flex;
					justify-content: space-between;
					.replayl {
						height: rem(60px);
						display: flex;
						.replayp {
							width: rem(60px);
							height: rem(60px);
							margin-right: rem(10px);
							img {
								width: 100%;
							}
						}
						.replayz {
							.floor {
								color: #0b364b88;
							}
						}
					}
					.replayr {
						i {
							font-size: rem(30px);
							color: #bbb;
						}
						span {
							font-size: rem(25px);
							color: #777;
						}
					}
				}
				.replay-con {
					font-size: 14px;
					color: #222;
					line-height: rem(50px);
					margin-bottom: rem(30px);
				}
			}
		}
	}
</style>


