import Vue from 'vue'
import Router from 'vue-router'

// 引入一级组件
import Index from '@/components/Index'
import New from '@/components/New'
import About from '@/components/About'

// 引入主题相关的组件
import All from '@/components/topics/All'
import Ask from '@/components/topics/Ask'
import Good from '@/components/topics/Good'
import Job from '@/components/topics/Job'
import Share from '@/components/topics/Share'
import Dev from '@/components/topics/Dev'
//主题详情页
import Show from '@/components/topics/Show'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'Index',
      component: Index,
      children:[
        {
          path: '/',
          component:All
        },
        {
          path: 'good',
          component:Good
        },
        {
          path: 'dev',
          component:Dev
        },
        {
          path: 'share',
          component:Share
        },
        {
          path: 'ask',
          component:Ask
        },
        {
          path: 'job',
          component:Job
        },
        {
        	path:'show/:id',
        	component:Show
        }
      ]
    },
    {
   		path: '/new',
   		name: 'New',
   		component: New
    },
    {
   		path: '/about',
   		name: 'About',
   		component: About
    },
    // 路由跳转
    {
      path:'/*',
      redirect:'/index'
    }
  ]
})



